import Vue from "vue";
import VueRouter from "vue-router";
import _2048 from "../views/2048.vue";
import Snake from "../views/snake.vue";
import Sokoban from "../views/sokoban.vue";
import FiveChess from "../views/fiveChess.vue";
import PixelBird from "../views/pixelBird.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "*",
    redirect: "/sokoban"
  },
  {
    path: "/sokoban",
    name: "sokoban",
    component: Sokoban
  },
  {
    path: "/snake",
    name: "snake",
    component: Snake
  },
  {
    path: "/fiveChess",
    name: "fiveChess",
    component: FiveChess
  },
  {
    path: "/pixelBird",
    name: "pixelBird",
    component: PixelBird
  },
  {
    path: "/2048",
    name: "2048",
    component: _2048
  }
];

const router = new VueRouter({
  routes
});
router.beforeEach((to, from, next) => {
  let oldSpan = document.getElementsByClassName("click_fivexu_active");
  [...oldSpan].forEach((item, index) => {
    item.remove();
  });
  next();
});

export default router;
