<template>
  <div id="app" @touchmove.stop.prevent ref="appDom" @click.stop="appClick">
    <video muted loop :autoplay="Utils.isPC()" :poster="imgSrc" v-show="isVideoLoad" ref="videoDom">
      当前浏览器不支持Video播放,请更新浏览器
      <source src="./assets/bg.mp4" type="video/mp4" />
    </video>
    <img v-if="!isVideoLoad" class="bg" :src="imgSrc" />
    <dotsRun ref="dotsRunDom"></dotsRun>
    <transition name="opacity">
      <times class="time" v-if="isActive"></times>
    </transition>
    <ul class="nav" v-if="isPC">
      <li
        :class="{ ac: item.link === $route.path }"
        v-for="(item, index) in list"
        :key="index"
        @click="routerLink(item.link)"
      >{{ item.title }}</li>
    </ul>
    <ball v-if="!isPC" :navList="list"></ball>
    <div class="router_view">
      <router-view />
    </div>
    <div class="footer">
      @版权所有Fivexu: 页面UI来源FivexuUI
      <a
        href="https://fivexu.github.io/ui/#/button"
        target="black"
      >https://fivexu.github.io/ui/#/button</a>
    </div>
  </div>
</template>

<script>
import { BG } from "./assets/base64.js";
import { textArr } from "./config/clickText.js";

const ball = () => import("components/ball/ball.vue");
const times = () => import("components/time/time.vue");
const dotsRun = () => import("components/dotsRun/dotsRun.vue");

const SHOW_TIME = 10000;
export default {
  data() {
    return {
      // 移动端导航显示
      mobileNavShow: false,
      // 判断当前使用平台
      isPC: true,
      // 是否在使用 时间动画
      isActive: true,
      // 视频加载完成
      isVideoLoad: true,
      // 图片
      imgSrc: BG,
      list: [
        { title: "推箱子", link: "/sokoban" },
        { title: "贪吃蛇", link: "/snake" },
        { title: "五子棋", link: "/fiveChess" },
        { title: "像素鸟", link: "/pixelBird" },
        { title: "2048", link: "/2048" }
      ]
    };
  },
  methods: {
    // 跳转
    routerLink(link) {
      this.$router.push(link);
    },
    // app点击事件
    appClick(ev) {
      if (
        this.mobileNavShow &&
        this.$refs.mobileNavDom &&
        this.$refs.mobileNavDom !== ev.target &&
        !this.$refs.mobileNavDom.contains(ev.target)
      ) {
        this.mobileNavShow = false;
      }
      let oldSpan = document.getElementsByClassName("click_fivexu_active");
      if (oldSpan.length > 10) {
        [...oldSpan].forEach((item, index) => {
          if (index <= 5) item.remove();
        });
      }
      let span = document.createElement("span");
      span.className = "click_fivexu_active";
      span.innerHTML = textArr[Math.floor(Math.random() * textArr.length)];
      span.style.left = `${ev.clientX - 300 / 2}px`;
      span.style.top = `${ev.clientY - 15}px`;
      this.$refs.appDom.appendChild(span);
    },
    // 鼠标移动和键盘点击
    mouseMove(ev) {
      this.isActive = false;
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        this.$fivexu.message.show({
          message: `超过${SHOW_TIME / 1000}秒未动,将显示当前时间!`
        });
        this.isActive = true;
      }, SHOW_TIME);
    },
    resize() {
      this.isPC = this.Utils.isPC();
    }
  },
  destroyed() {
    clearTimeout(this.timer);
    window.removeEventListener("resize", this.resize, true);
    window.removeEventListener("mousemove", this.mouseMove, true);
    window.removeEventListener("keydown", this.mouseMove, true);
    window.removeEventListener("click", this.mouseMove, true);
    window.removeEventListener("touchstart", this.mouseMove, true);
  },
  mounted() {
    this.$nextTick(() => {
      window.addEventListener("resize", this.resize, true);
      window.addEventListener("mousemove", this.mouseMove, true);
      window.addEventListener("keydown", this.mouseMove, true);
      window.addEventListener("click", this.mouseMove, true);
      window.addEventListener("touchstart", this.mouseMove, true);
      this.$success(`超过${SHOW_TIME / 1000}秒未动,将有时间动画!`);
      this.$refs.videoDom.addEventListener("canplay", () => {
        this.isVideoLoad = true;
      });
    });
  },
  components: {
    ball,
    times,
    dotsRun
  }
};
</script>

